import { createApp } from 'vue';
import lax from 'lax.js';
import AOS from 'aos';
import 'aos/dist/aos.css';
import fadeInText from '@/lib/fadeInText';
import App from './App.vue';
import router from './router';
import store from './store';
import rotateCard from './lib/rotateCard';

const app = createApp(App).use(store).use(router).use(lax)
  .use(AOS.init());

app.directive('fadeInText', fadeInText);
app.directive('rotateCard', rotateCard);

app.mount('#app');
