<script>
import Banner from '@/components/Banner.vue';
import {
  computed,
  onMounted, reactive, ref,
} from 'vue';
import lax from 'lax.js';
import Star from '@/lib/Star';

export default {
  components: {
    Banner,
  },
  setup() {
    // activity canvas start
    const canvas = ref(null);
    const ctx = ref(null);
    const allStar = reactive({
      content: [],
    });
    const starCount = ref(1500);
    const color = ref('#FFFFFF');
    const initStar = computed(() => {
      const eachStar = [];
      for (let i = 0; i < starCount.value; i += 1) {
        // 每顆星星大小
        const randomRadius = Math.random() * 2;
        const x = Math.random() * (canvas.value.width + 1000) - (canvas.value.width + 1000) / 3;
        const y = Math.random()
        * (canvas.value.height + 1000) - (canvas.value.height + 1000) / 2;
        eachStar.push(new Star(x, y,
          randomRadius, color.value));
      }
      return eachStar;
    });
    allStar.content = initStar;
    const draw = (x, y, radius) => {
      ctx.value.beginPath();
      ctx.value.arc(x, y, radius, 0, Math.PI * 2, false);
      ctx.value.shadowColor = color;
      ctx.value.shadowBlur = 15;
      ctx.value.fillStyle = color.value;
      ctx.value.fill();
      ctx.value.closePath();
    };
    const animate = () => {
      allStar.content.forEach((item) => {
        draw(item.x, item.y, item.radius);
      });
    };
    // activity canvas end
    onMounted(() => {
      canvas.value.width = window.innerWidth;
      canvas.value.height = window.innerHeight / 3;
      ctx.value = canvas.value.getContext('2d');
      animate();
      window.addEventListener('resize', () => {
        canvas.value.width = window.innerWidth;
        canvas.value.height = window.innerHeight / 3;
        animate();
      });
      window.onload = () => {
        lax.init();

        // Add a driver that we use to control our animations
        lax.addDriver('scrollY', () => window.scrollY);

        // Add animation bindings to elements
        lax.addElements('.selector', {
          scrollY: {
            translateX: [
              ['elOutY', 'elCenterY', 'elInY'],
              [0, 'screenWidth/2', 'screenWidth'],
            ],
          },
        });
      };
    });
    return {
      canvas,
    };
  },
};
</script>

