import { gsap } from 'gsap';

const animationScroll = new IntersectionObserver((entries, scroll) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      gsap.to(entry.target, {
        y: 0,
        duration: 1,
        stagger: 0.5,
      });
      scroll.unobserve(entry.target);
    }
  });
});

export default {
  mounted(ele) {
    const el = ele;
    el.style.transform = 'translateY(100%)';
    animationScroll.observe(el);
  },
};
