<script>
import {
  onMounted, ref, reactive, computed, onUpdated,
} from 'vue';
import Star from '@/lib/Star';

export default {
  name: 'Home',
  components: {},
  setup() {
    const canvas = ref(null);
    const ctx = ref(null);
    const starCount = ref(1500);
    const allStars = reactive({ content: [] });
    const opacity = ref(1);
    const mouseDown = ref(false);
    const timer = ref(0);
    const speed = ref(0.0005);
    const colors = reactive({
      value: [
        '#2185C5',
        '#7ECEFD',
        '#FFF6E5',
        '#FF7F66',
        '#F2E8C9',
      ],
    });
    const initStars = computed(() => {
      const countStar = [];
      for (let i = 0; i < starCount.value; i += 1) {
        // 星星顏色
        const randomColorIndex = Math.floor(Math.random() * colors.value.length);
        // 每顆星星大小
        const randomRadius = Math.random() * 2;
        const x = Math.random() * (canvas.value.width + 1000) - (canvas.value.width + 1000) / 2;
        const y = Math.random() * (canvas.value.height + 1000) - (canvas.value.height + 1000) / 2;
        countStar.push(new Star(x, y,
          randomRadius, colors.value[randomColorIndex]));
      }
      return countStar;
    });
    function draw(x, y, radius, color) {
      ctx.value.beginPath();
      ctx.value.arc(x, y, radius, 0, Math.PI * 2, false);
      ctx.value.shadowColor = color;
      ctx.value.shadowBlur = 15;
      ctx.value.fillStyle = color;
      ctx.value.fill();
      ctx.value.closePath();
    }
    const animate = () => {
      window.requestAnimationFrame(animate);
      ctx.value.save();
      if (mouseDown.value) {
        const desiredOpacity = 0.01;
        opacity.value += (desiredOpacity - opacity.value) * 0.03;
        ctx.value.fillStyle = `rgba(18, 18, 18, ${opacity.value})`;
        const desiredSpeed = 0.012;
        speed.value += (desiredSpeed - speed.value) * 0.01;
        timer.value += speed.value;
      } else if (!mouseDown.value) {
        const originalOpacity = 1;
        opacity.value += (originalOpacity - opacity.value) * 0.01;
        ctx.value.fillStyle = `rgba(18, 18, 18, ${opacity.value})`;
        const originalSpeed = 0.001;
        speed.value += (originalSpeed - speed.value) * 0.01;
        timer.value += speed.value;
      }
      ctx.value.fillRect(0, 0, canvas.value.width, canvas.value.height);
      ctx.value.translate(canvas.value.width / 2, canvas.value.height / 2);
      ctx.value.rotate(timer.value);
      allStars.content.forEach((star) => {
        draw(star.x, star.y, star.radius, star.colors);
      });
      ctx.value.restore();
    };
    const addSpeed = () => {
      mouseDown.value = !mouseDown.value;
    };
    onMounted(() => {
      ctx.value = canvas.value.getContext('2d');
      canvas.value.height = window.innerHeight;
      canvas.value.width = window.innerWidth;
      window.addEventListener('resize', () => {
        canvas.value.height = window.innerHeight;
        canvas.value.width = window.innerWidth;
        allStars.content = initStars;
      });
      allStars.content = initStars;
      animate();
    });
    onUpdated(() => {
      ctx.value = canvas.value.getContext('2d');
      canvas.value.height = window.innerHeight;
      canvas.value.width = window.innerWidth;
      window.addEventListener('resize', () => {
        canvas.value.height = window.innerHeight;
        canvas.value.width = window.innerWidth;
        allStars.content = initStars;
      });
      allStars.content = initStars;
      allStars.content.forEach((star) => {
        draw(star.x, star.y, star.radius, star.colors);
      });
    });
    return {
      canvas,
      ctx,
      colors,
      addSpeed,
    };
  },
};
</script>

<template>
    <section class="banner position-relative d-flex justify-content-center align-items-center
    z-index-100">
        <div class="button position-relative d-inline-block">
            <span class="position-absolute d-block"></span>
            <span class="position-absolute d-block"></span>
            <span class="position-absolute d-block"></span>
            <span class="position-absolute d-block"></span>
            <button class="text-nowrap py-2">
                check it out
            </button>
        </div>
        <canvas ref="canvas"
        class="position-absolute z-index-n1"
        @mousedown="addSpeed" @mouseup="addSpeed"></canvas>
    </section>
</template>

